body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1030;
  display: flex;
  border-bottom: solid 1px #e1e1f1;
  background-color: #E45826;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  /* div for logo and nav menu */
  /* div for nav menu */
  /* a.text-link{
      display: flex;
      justify-content: space-evenly;
  } */
  /* Style the active class, and buttons on mouse-over */
}
header div.container {
  display: flex;
}
header div.me-auto.nav {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  margin-left: 250px;
  color: #E6D5B8;
}
header a.icon.navbar-brand {
  padding: 0px;
  margin-right: 0px;
}
header .icon {
  color: #F0A500;
  font-family: "Trebuchet MS", sans-serif;
  font-size: 40px;
  margin-bottom: 14px;
  float: left;
}
header .title {
  margin: 0 20px 10px 10px;
  color: #E6D5B8;
  font-family: "Trebuchet MS", sans-serif;
  font-size: 40px;
  padding-top: 3px;
}
header .btn {
  border: none;
  outline: none;
  padding: 10px 16px;
  background-color: #f1f1f1;
  cursor: pointer;
  font-size: 18px;
}
header .active {
  color: white;
}
.text-link {
  margin: 10px;
  margin-bottom: 0px;
  padding: 10px;
  float: "right";
  text-decoration: none;
  margin-left: 5px;
  color: inherit;
  justify-content: space-between;
}
.text-link:hover {
  color: white;
}

.menu {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

div.me-auto.nav {
  margin-left: 20px;
}

#loggedAs:hover {
  color: inherit;
}
.formDiv {
  float: right;
  background: #7f7f7f;
  background: rgba(255, 255, 255, 0.35);
  margin-top: 50px;
  margin-right: 200px;
  padding: 20px;
  font-family: Verdana, sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: black;
  border-style: dashed;
}

div.row {
  height: 100vh;
}

div.col-sm-6 {
  background-image: url(/pizza/static/media/pizza.503be865.jpg);
  background-size: cover;
  height: 450px;
  margin-top: 50px;
  text-align: center;
}

button.btn.btn-warning {
  font-family: Verdana, sans-serif;
  font-size: 25px;
  color: black;
  margin-top: 20px;
}
button.btn.btn-warning:hover {
  text-decoration: underline;
}

.formBanner {
  margin-top: 50px;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
  color: black;
  background: #7f7f7f;
  background: rgba(255, 255, 255, 0.6);
  border-style: dashed;
  padding: 20px;
}

.field {
  margin: 10px;
  float: "right";
}
.field input {
  justify-content: space-between;
}

button {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.4);
}

html {
  height: 100vh;
}

.error {
  color: red;
  font-size: 14px;
}

