main {
  background-image: url(/pizza/static/media/smile.8d1c7ef7.jpg);
  background-size: cover;
  width: 100%;
  height: 110vh;
  object-fit: cover;
  align-items: center;
}

.banner {
  background: #7f7f7f;
  background: rgba(255, 255, 255, 0.35);
  margin-top: 150px;
  margin-left: 0px;
  margin-right: 100px;
  padding: 20px;
  font-family: Verdana, sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: black;
  border-style: dashed;
}

button.btn.btn-outline-light.btn-lg {
  margin-left: 850px;
  background-color: #F0A500;
  color: #1B1A17;
  border-color: #1B1A17;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.4);
}
button.btn.btn-outline-light.btn-lg:hover {
  text-decoration: underline;
}

